import { useState } from "react";
import styled from "styled-components";

import { EngLoc } from "../../assets/constants/localizations";

const SToggler = styled.div`
  padding: 8px;
  height: 64px;
  background: rgba(255, 255, 255, 0.15);
  border: 2px solid rgba(255, 255, 255, 0.1);
  border-radius: 48px;
  display: flex;
  gap: 5px;
  `;

const Variant = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  height: 64px;

  padding: 0 58px;
  border-radius: 51px;

  font-size: 22px;
  line-height: 27px;
  font-weight: 300;
  color: #ffffff;

  background: transparent;
  cursor: pointer;

  ${({ active }) => active && `background: #0e7ac2;`}
  
  transition: 0.25s ease-in-out;
`;

const Toggler = ({language}) => {
  const [isActive, setIsActive] = useState(1);

  return (
    <SToggler>
      <Variant active={isActive == 1} onClick={() => setIsActive(1)}>
        {language === "Ru" ? "Рекламные агенства" : EngLoc.partners.partners_slider.toggler[0]}
      </Variant>
      <Variant active={isActive == 2} onClick={() => setIsActive(2)}>
        {language === "Ru" ? "Бренды" : EngLoc.partners.partners_slider.toggler[1]}
      </Variant>
    </SToggler>
  );
};

export default Toggler;
