import { useEffect, useRef, useState } from "react";
import styled from "styled-components";
import arrow from "../../assets/images/Arrow.svg";

const SSlider = styled.div`
  display: flex;
  gap: 35px;
  width: 1720px;
  justify-content: space-between;
  align-items: center;

  & > img {
    opacity: 0.6;
    transition: 0.25s;
    user-select: none;
  }

  & > img:hover {
    opacity: 1;
  }

  @media (max-width: 1750px) {
    width: 1420px;
  }

  @media (max-width: 1400px) {
    width: 920px;
  }
`;

const SliderBox = styled.div`
  position: relative;
  width: 1620px;
  min-height: 715px;
  overflow: hidden;

  @media (max-width: 1750px) {
    width: 1200px;
  }

  @media (max-width: 1400px) {
    width: 800px;
  }
`;

const SliderInner = styled.div`
  transition: 0.75s ease-in-out;
`;

const ArrowLeft = styled.img`
  height: clamp(40px, 3vw, 52px);
  cursor: pointer;
  transform: rotate(180deg);
`;

const ArrowRight = styled.img`
  height: clamp(40px, 3vw, 52px);
  cursor: pointer;
`;

const Slider = ({ stepMargin, steps, children }) => {
  const [step, setStep] = useState(0);
  useEffect(() => {
    if (step > steps) setStep(0);
    if (step < 0) setStep(steps);

    Inner.current.setAttribute(
      "style",
      `transform: translateX(-${ step == 0 ? 0 : stepMargin * step}px)`
    );
  }, [step]);

  const Inner = useRef(null);

  return (
    <SSlider>
      <ArrowLeft
        src={arrow}
        alt="arrowLeft"
        onClick={() => setStep(step - 1)}
      />
      <SliderBox>
        <SliderInner ref={Inner} style={{ marginLeft: 0 }}>
          {children}
        </SliderInner>
      </SliderBox>
      <ArrowRight
        src={arrow}
        alt="arrowRight"
        onClick={() => setStep(step + 1)}
      />
    </SSlider>
  );
};

export default Slider;
