import styled from "styled-components";
import { EngLoc } from "../../../assets/constants/localizations";
import { Text, Title3 } from "../../../assets/styles/component";
import Button from "../../../assets/UI/ButtonDesktop";

const SCase = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 24px;

  position: relative;

  width: 428px;
  min-width: 428px;
  height: 715px;
  max-width: 520px;
  max-height: 715px;

  padding: 40px;

  text-align: left;
  border: 2px solid #ffffff2a;
  border-radius: 34px;

  & > *:not(:last-child) {
    z-index: 1;
  }

  @media (max-width: 1750px) {
    width: 500px;
    min-width: 500px;
  }

  @media (max-width: 1400px) {
    width: 500px;
    min-width: 500px;
    margin: 0 100px;
  }
`;

const Title = styled(Title3)`
  line-height: 130%;
`;

const Description = styled(Text)``;

const Categories = styled.div`
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-bottom: 26px;
`;

const Category = styled.div``;

const TitleValue = styled(Text)``;

const Value = styled(Text)``;

const Case = ({
  title,
  desc,
  design,
  value1,
  value2,
  value3,
  valueTitle1,
  valueTitle2,
  valueTitle3,
  language,
}) => {
  return (
    <SCase>
      <Title>{title}</Title>
      <Description>{desc}</Description>
      <Categories>
        {value1 && (
          <Category>
            <TitleValue color="#FFFFFF80">
              {valueTitle1
                ? valueTitle1
                : language === "Ru"
                ? "Кампания"
                : EngLoc.partners.cases.period}
            </TitleValue>
            <Value size={36} weight={600}>
              {value1}
            </Value>
          </Category>
        )}
        {value2 && (
          <Category>
            <TitleValue color="#FFFFFF80">
              {valueTitle2
                ? valueTitle2
                : language === "Ru"
                ? "Подписчиков"
                : EngLoc.partners.cases.subscribers}
            </TitleValue>
            <Value size={36} weight={600}>
              {value2}
            </Value>
          </Category>
        )}
        {value3 && (
          <Category>
            <TitleValue color="#FFFFFF80">
              {valueTitle3
                ? valueTitle3
                : language === "Ru"
                ? "Просмотров"
                : EngLoc.partners.cases.views
              }
            </TitleValue>
            <Value size={36} weight={600}>
              {value3}
            </Value>
          </Category>
        )}
      </Categories>
      <Button type="secondary" link={true}>
        {language === "Ru" ? "Подробнее" : EngLoc.buttons.more}
      </Button>
      {design}
    </SCase>
  );
};

export default Case;
