import styled from "styled-components";
import { css } from "styled-components";

import { EngLoc } from "../../../assets/constants/localizations";

import { Title2 } from "../../../assets/styles/component";
import Case from "./CaseDesktop";
import Slider from "../SliderDesktop";

import SberDesignSvg from "../../../assets/images/SberDesignSvg.svg";
import SKSkolkovoDesignSvg from "../../../assets/images/SKSkolkovoDesignSvg.svg";
import WallStreetDesignSvg from "../../../assets/images/WallStreetDesignSvg.svg";
import GlamourDesignSvg from "../../../assets/images/GlamourDesignSvg.svg";
import BKSDesignSvg from "../../../assets/images/BKSDesignSvg.svg";
import OKKODesignSvg from "../../../assets/images/OKKODesignSvg.svg";

const SCasesInner = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  gap: 120px;
`;

const CasesBox = styled.div`
  display: flex;
  gap: 20px;
`;

const design = css`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 32px;
  z-index: 0;
  overflow: hidden;

  & > img {
    position: absolute;
    bottom: 0;
    right: 0px;
    width: 100%;
  }
`;

const BKCDesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg, #1B6FFF 4.1%, #6BA8FF 142.69%);
`;

const OKKODesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg, #210133 4.1%, #380A4B 142.69%);
`;

const SKSDesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg, #593FF5 4.1%, #9A7FFC 142.69%);

  & > img {
    bottom: -30px;
  }
`;

const GlamourDesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg, #E71C8D 4.1%, #FFB6E0 142.69%);
`;

const WallStreetDesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg, #E03D46 4.1%, #023260 86.87%);
`;

const SberDesign = styled.div`
  ${design}
  background: linear-gradient(243.39deg,  #17B82E 4.1%, #8FFF36 142.69%);
`;

const CasesInner = ({ language }) => {
  const Cases = [
    {
      title: "БКС Премьер",
      desc:
        language === "Ru"
          ? `БКС презентовал свои услуги и возможность инвестирования в продукты брокера для умножения своих средств`
          : EngLoc.partners.cases.cases_list[0],
      value1: "8 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "316к",
      value3: "62к",
      design: (
        <BKCDesign>
          <img loading="lazy" src={BKSDesignSvg} alt="BKS"/>
        </BKCDesign>
      ),
    },
    {
      title: "OKKO",
      desc:
        language === "Ru"
          ? `Реклама челленджа для киноманов от онлайн-кинотеатра Окко`
          : EngLoc.partners.cases.cases_list[1],
      value1: "11 " + (language === "Ru" ? "Дней" : "Days"),
      valueTitle2:  language === "Ru" ? "Охват" : EngLoc.partners.cases.coverage,
      value2: "433к",
      design: (
        <OKKODesign>
          <img loading="lazy" src={OKKODesignSvg} alt="OKKO"/>
        </OKKODesign>
      ),
    },
    {
      title: "SK Сколково",
      desc:
        language === "Ru"
          ? `Рекламная кампания для Бизнес-школы Сколково в Телеграм -
  продвижение мастер-класса от Тома Роулинса. Как результат:
  sold-out`
          : EngLoc.partners.cases.cases_list[2],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "316к",
      value3: "79к",
      design: (
        <SKSDesign>
          <img loading="lazy" src={SKSkolkovoDesignSvg} alt="SKSkolkovo"/>
        </SKSDesign>
      ),
    },
    {
      title: "Glamour",
      desc:
        language === "Ru"
          ? `Размещения в Телеграм для Glamour - тестирование нового инструмента для крупного глянцевого издания`
          : EngLoc.partners.cases.cases_list[3],
      value1: "1 " + (language === "Ru" ? "Месяц" : "Month"),
      value2: "5к",
      value3: "183к",
      design: (
        <GlamourDesign>
          <img loading="lazy" src={GlamourDesignSvg} alt="Glamour"/>
        </GlamourDesign>
      ),
    },
    {
      title: "Wall Street English",
      desc:
        language === "Ru"
          ? `Реклама школы английского языка на жителей Москвы с помощью посевов на всех площадках SocialJet`
          : EngLoc.partners.cases.cases_list[4],
      value1: "5 " + (language === "Ru" ? "Месяцев" : "Months"),
      value2: "2к",
      value3: "4,7 " + (language === "Ru" ? "Млн" : "Million"),
      design: (
        <WallStreetDesign>
          <img loading="lazy" src={WallStreetDesignSvg} alt="WallStreet"/>
        </WallStreetDesign>
      ),
    },
    {
      title: "Сбербанк",
      desc:
        language === "Ru"
          ? `Сбербанк анонсирует запуск проекта инвестирования 500 лучших стартапов`
          : EngLoc.partners.cases.cases_list[5],
      value1: "12 " + (language === "Ru" ? "Дней" : "Days"),
      value2: "898к",
      value3: "216к",
      design: (
        <SberDesign>
          <img loading="lazy" src={SberDesignSvg} alt="Sber"/>
        </SberDesign>
      ),
    },
  ];
  
  const stepMargins = () => {
    let margin = 1620;
    let screenWidth = window.screen.width;

    if (screenWidth > 1750) margin = 530;
    if (screenWidth > 1400 && screenWidth < 1750) margin = 600;
    if (screenWidth < 1400) margin = 800;

    return margin;
  };

  const stepsCount = () => {
    let screenWidth = window.screen.width;
    let steps = 2;

    if (screenWidth > 1750) steps = 3;

    if (screenWidth > 1400 && screenWidth < 1750) steps = 4;

    if (screenWidth < 1400) steps = 5;

    return steps;
  };

  return (
    <SCasesInner id={"cases"}>
      <Title2>
        {language === "Ru" ? "Кейсы" : EngLoc.partners.cases.title}
      </Title2>
      <Slider stepMargin={stepMargins()} steps={stepsCount()}>
        <CasesBox>
          {Cases.map((item) => (
            <Case
              language={language}
              key={Math.random()}
              title={item.title}
              desc={item.desc}
              value1={item.value1}
              value2={item.value2}
              value3={item.value3}
              valueTitle1={item.valueTitle1}
              valueTitle2={item.valueTitle2}
              valueTitle3={item.valueTitle3}
              design={item.design}
            />
          ))}
        </CasesBox>
      </Slider>
    </SCasesInner>
  );
};

export default CasesInner;
